				material:"泰国天然乳胶原料",
				price:"228.0",
				collect:false
			},{
				id:2,
				img:"../../static/img/4.png",
				title:"泰国平面波浪枕",
				material:"泰国天然乳胶原料",
				price:"228.0",
				collect:false
			},{
				id:3,
				img:"../../static/img/4.png",
				title:"泰国平面波浪枕",
				material:"泰国天然乳胶原料",
				price:"228.0",
				collect:false
			}],
			collectList:[]
		}
	},
	methods: {
		collect(e){
			// this.collectList=[]
			this.newsList[e.currentTarget.dataset.id].collect=!this.newsList[e.currentTarget.dataset.id].collect
			// for(var i=0;i<this.newsList.length;i++){
			// 	if(this.newsList[i].collect){
			// 		this.collectList.push(this.newsList[i])
			// 		uni.setStorage({
			// 			key:'collectList',
			// 			data:JSON.stringify(this.collectList)
			// 		})
			// 	}
			// }
			// uni.getStorage({
			// 	key:'collectList',
			// 	success(e){
			// 		console.log(JSON.parse(e.data))
			// 	}
			// })
			uni.setStorage({
				key:'collectList',
				data:JSON.stringify(this.newsList)
			})
		},
		goDetail(e){
			// console.log(e.currentTarget.dataset.id)
			wx.navigateTo({
				url:"../details/details?item="+JSON.stringify(this.newsList[e.currentTarget.dataset.index])
			})
		}
	},
	onLoad(){
		uni.setStorage({
			key:'collectList',
			data:JSON.stringify(this.newsList)
		})
	},
	onShow(){
		var that=this
		uni.getStorage({
			key:'collectList',
			success(e){
				that.newsList=JSON.parse(e.data)
			}
		})
	}
}
